import React from 'react';
import {
    Wrapper,
    InnerWrapper,
    Item,
    ImageWrapper,
} from './Styled';

import hkd from 'assets/hkd.jpg'
import chd from 'assets/chd.jpg'

const images = [
    hkd,
    chd,
]

const Cube = () => {
    const Images = images.map((image) => {
        return (
            <Item>
                <ImageWrapper>
                    <img src={image} />
                </ImageWrapper>
            </Item>
        )
    })
    return (
        <Wrapper>
            <InnerWrapper>
                {Images}
            </InnerWrapper>
        </Wrapper>
    )
}

export default Cube;